* {
  box-sizing: border-box;
  margin: 0;
}

html {
  padding: 0;
  margin: 0;
}

body {
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 0;
}

video {
  position: absolute;
  z-index: 0;
  width: 100%;
  height: 100vh;
  object-fit: cover;
  margin: 0;
  padding: 0;
  top: 0px;
  left: 0px;
  border: none;
  filter: brightness(60%) contrast(60%) invert(20%);
}

.App {
  position: absolute;
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  overflow: hidden;
  z-index: 2;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

#tag {
  font-size: small;
  font-weight: 500;
  position: relative;
  top: 25px;
}

.blob {
  background-color: white;
  border-radius: 15px;
  padding: 10px;
  opacity: 0.8;
}

header {
  font-size: 69px;
  white-space: nowrap;
  position: absolute;
  top: 220px;
  left: 50%;
  transform: translateX(-50%);
}

button {
  opacity: 1;
  border-radius: 50px;
  border: none;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  font-weight: bolder;
  background: white;
  font-size: larger;
  height: 40px;
  width: 95px;
  transition: all 0.2s;
}

.arrow {
  line-height: 6px;
}

.main {
  font-size: medium;
}

button:hover {
  background: #ffb703;
}

#SessionSelector {
  left: 66%;
}

#BreakSelector {
  left: 33%;
}

.selectors {
  position: absolute;
  top: 37%;
  font-size: 35px;
  display: grid;
  grid-template-columns: auto auto;
  grid-column-gap: 20px;
  transform: translateX(-50%);
}
.selectors button {
  margin: 0 auto;
}
.selectors div {
  margin: 0 auto;
  grid-column: 1/span 2;
}
.selectors span {
  margin: 0 auto;
  grid-column: 1/span 2;
}

#mainDisplay {
  position: absolute;
  top: 65%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 10px;
  width: 25%;
  height: 20%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-size: 55px;
}

button:hover {
  animation: pulse 1s;
  box-shadow: 0 0 0 2em transparent;
}